//*************
//* Animation *
//*************

//********
//* logo *
//********

@include keyframes(drawTopGuides) {
  0% {
    @include transform(translate(-42px, 0px)); }
  40% {
    @include transform(translate(40px, 0px)); }
  60% {
    @include transform(translate(-20px, 0px)); }
  100% {
    @include transform(translate(0px, 0px)); } }
@include keyframes(drawLeftGuides) {
  0% {
    @include transform(translate(0px, -43px)); }
  40% {
    @include transform(translate(0px, 14px)); }
  60% {
    @include transform(translate(0px, -12px)); }
  100% {
    @include transform(translate(0px, 0px)); } }
@include keyframes(drawBottomGuides) {
  0% {
    @include transform(translate(44px, 0px)); }
  40% {
    @include transform(translate(-30px, 0px)); }
  60% {
    @include transform(translate(32px, 0px)); }
  100% {
    @include transform(translate(0px, 0px)); } }
@include keyframes(drawRightGuides) {
  0% {
    @include transform(translate(0px, 45px)); }
  40% {
    @include transform(translate(0px, -60px)); }
  60% {
    @include transform(translate(0px, 12px)); }
  100% {
    @include transform(translate(0px, 0px)); } }
@include keyframes(quarter) {
  0% {
    opacity: 0;
    @include transform(rotateZ(90deg)); }
  40% {
    opacity: .8;
    @include transform(rotateZ(-12deg)); }
  60% {
    opacity: .35;
    @include transform(rotateZ(12deg)); }
  100% {
    opacity: 1;
    @include transform(rotateZ(0deg)); } }
@include keyframes(half) {
  0% {
    opacity: 0;
    @include transform(rotateZ(180deg)); }
  40% {
    opacity: .8;
    @include transform(rotateZ(-10deg)); }
  60% {
    opacity: .35;
    @include transform(rotateZ(10deg)); }
  100% {
    opacity: 1;
    @include transform(rotateZ(0deg)); } }
@include keyframes(three-quarter) {
  0% {
    opacity: 0;
    @include transform(rotateZ(270deg)); }
  40% {
    opacity: .8;
    @include transform(rotateZ(-5deg)); }
  60% {
    opacity: .35;
    @include transform(rotateZ(20deg)); }
  100% {
    opacity: 1;
    @include transform(rotateZ(0deg)); } }
@include keyframes(full) {
  0% {
    opacity: 0;
    @include transform(rotateZ(-360deg)); }
  40% {
    opacity: .8;
    @include transform(rotateZ(-10deg)); }
  60% {
    opacity: .35;
    @include transform(rotateZ(10deg)); }
  100% {
    opacity: 1;
    @include transform(rotateZ(0deg)); } }
//********
//* menu *
//********
@include keyframes(roll_in) {
  0% {
    opacity: 0;
    @include transform(translate(-20px, 14px) scale(1) rotateZ(-90deg)); }
  20% {
    opacity: 0.67;
    @include transform(translate(40px, 14px) scale(6) rotateZ(60deg)); }
  80% {
    opacity: 1;
    width: 1px;
    height: 1px;
    color: hsla(0, 0%, 0%, 0);
    font-size: 0em;
    background-color: $accent;
    @include transform(translate(70px, 14px) scale(8) rotateZ(166deg)); }
  100% {
    opacity: 1;
    width: 48px;
    height: 28px;
    color: $gray-40;
    font-size: 0.75em;
    background-color: $gray-12;
    @include transform(translate(0px, 0px) scale(1) rotateZ(0deg)); } }
@include keyframes(falling_box) {
  0% {
    top: -300px;
    height: 2px;
    @include transform(scale(6)); }
  20% {
    top: 28px;
    height: 2px;
    @include transform(scale(.8)); }
  80% {
    top: -10px;
    height: 2px;
    @include transform(scale(2)); }
  100% {
    top: 0;
    height: 28px;
    @include transform(scale(1)); } }
@include keyframes(dropDown) {
  0% {
    @include transform(translateY(-80px)); }
  30% {
    @include transform(translateY(0px)); }
  53% {
    @include transform(translateY(-40px)); }
  100% {
    @include transform(translateY(0px)); } }
@include keyframes(signaling) {
  0% {
    opacity: 1;
    stroke: $gray-60;
    stroke-width: 12px;
    @include transform(scale(.8)); }
  100% {
    opacity: .5;
    stroke: $gray-60;
    stroke-width: 0px;
    @include transform(scale(1)); } }

@include keyframes(expand_size_down) {
  0% {
    @include transform(translateY(-40px)); }
  50% {
    @include transform(translateY(0)); }
  84% {
    @include transform(translateY(-8px)); }
  100% {
    @include transform(translateY(0)); } }
@include keyframes(expand_size_up) {
  0% {
    @include transform(translateY(40px)); }
  50% {
    @include transform(translateY(0)); }
  84% {
    @include transform(translateY(8px)); }
  100% {
    @include transform(translateY(0)); } }
@include keyframes(expand_gap) {
  0% {
    @include transform(translateX(14px)); }
  50% {
    @include transform(translateX(-20px)); }
  84% {
    @include transform(translateX(12px)); }
  100% {
    @include transform(translateX(0)); } }
@include keyframes(expand_gap_opposite) {
  0% {
    @include transform(translateX(-14px)); }
  50% {
    @include transform(translateX(20px)); }
  84% {
    @include transform(translateX(-12px)); }
  100% {
    @include transform(translateX(0)); } }
@include keyframes(expand_gap_scale) {
  0% {
    @include transform(translateY(40px)); }
  50% {
    @include transform(translateY(0)); }
  84% {
    @include transform(translateY(8px)); }
  100% {
    @include transform(translateY(0)); } }
