@mixin animation($contents) {
  -webkit-animation: $contents;
  animation:         $contents; }
@mixin transition($contents...) {
  -webkit-transition: $contents;
  transition:         $contents; }
@mixin transform($contents) {
  -webkit-transform:  $contents;
  transform:          $contents; }
@mixin transform-origin($contents) {
  -webkit-transform-origin: $contents;
  transform-origin:         $contents; }
@mixin backface-visibility($type) {
  -webkit-backface-visibility:  $type;
  backface-visibility:          $type; }
@mixin perspective($val) {
  -webkit-perspective:  $val;
  perspective:          $val; }
@mixin perspective-origin($val) {
  -webkit-perspective-origin: $val;
  perspective-origin:         $val; }
@mixin keyframes($animationName) {
  @-webkit-keyframes #{$animationName} {
    @content; }
  @keyframes #{$animationName} {
    @content; } }
